.Parallax {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 120vh;
  overflow: hidden;
  background-color: var(--bg-d2);
  top: 0;
}
.Parallax .img {
  width: 100%;
  height: 100%;
  position: absolute;
  -o-object-fit: cover;
     object-fit: cover;
  top: 0;
  left: 0;
  z-index: var(--i--n);
}
.Parallax .imgg {
  width: 100%;
  height: 130vh;
  position: fixed;
  -o-object-fit: cover;
     object-fit: cover;
  top: 0;
  left: 0;
  z-index: var(--i--n);
}
.Parallax .Fill {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4%;
  z-index: var(--i--n);
  background-color: var(--bg-Parallax);
}

@media (max-width: 767.98px) {
  .Parallax {
    height: 100vh;
  }
}/*# sourceMappingURL=Parallax.css.map */